.people {
  padding: 132px 0 113px;
  background: url("../img/star_people.svg") no-repeat left 12% top 12%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.people h2 {
  padding-bottom: 112px;
  position: relative;
}
.people h2::after {
  content: "";
  position: absolute;
  right: -28%;
  top: 33px;
  width: 114px;
  height: 80px;
  background: url("../img/arrow-people-title.svg") no-repeat;
}
.people__box {
  align-self: stretch;
  display: flex;
  padding: 82px 85px 69px 67px;
  border-radius: 30px;
  border: 2px solid rgba(255, 255, 255, 0.2);
  background: #2F2B6D url("../img/star_people-box.svg") bottom 77px right 33% no-repeat;
}
.people__img-box {
  display: flex;
  position: relative;
  padding-right: 93px;
}
.people__img-box::after {
  content: "";
  width: 6px;
  height: 95%;
  background-color: #4B4C84;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
.people__item {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-left: 1px;
}
.people__item > div {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
}
.people__item-name {
  color: #FFF;
  font-family: "Red Rose", sans-serif;
  font-size: 28px;
  font-weight: 700;
  line-height: 22px;
  padding-bottom: 23px;
}
.people__item-position {
  color: #D9ECFF;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}
.people__item-rate {
  display: flex;
  grid-column: 2/3;
  grid-row: 1/-1;
  justify-self: end;
  align-self: end;
}
.people__item p {
  color: #B0B0D1;
  font-size: 20px;
  font-weight: 400;
  line-height: 32px;
  padding-top: 66px;
}
.people__slider {
  background: url("../img/quotes.svg") bottom 16px right 5px no-repeat;
  margin-left: 30px;
}
.people__slider article {
  width: 100%;
  padding-left: 60px;
}
.people .slick-slider {
  max-width: 925px;
  overflow: hidden;
}
.people .slick-arrow {
  display: block;
  padding: 0;
  width: 35px;
  height: 35px;
  border: none;
  font-size: 0;
  z-index: 1;
  position: absolute;
  bottom: 30px;
  transition: background 0.3s;
}
.people .slick-arrow:hover {
  background: transparent url("../img/arrow_people-active.svg") no-repeat center;
}
.people .slick-prev {
  left: 55px;
  background: transparent url("../img/arrow_people.svg") no-repeat center;
  transform: rotate(180deg);
}
.people .slick-next {
  background: transparent url("../img/arrow_people.svg") no-repeat center;
  left: 104px;
}

@media (max-width: 1600px) {
